import { useTranslation } from "react-i18next";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../PlateformUser/PlateformUser.module.css";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
const LmsLeaveForDate = ({ onClose, allDate }) => {
  const { t } = useTranslation();
  return (
    <Modal onClose={onClose}>
      <div className={style.modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{t("leave_dates")}</h6>
            </div>
            <img
              src={`${assestsUserFinanceUrl}/close.svg`}
              alt="close icon"
              onClick={onClose}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0.3rem",
          }}
        >
          <div className={style.assign_each_paragraph}>
            {allDate.map((dt) => (
              <p key={dt}>{getDateByMonth(dt)}, </p>
            ))}
          </div>
        </UserMemberContentWrapper>
      </div>
    </Modal>
  );
};

export default LmsLeaveForDate;

const allMonth = [
  "January",
  "February",
  "March",
  "April",
  "May",
  "June",
  "July",
  "August",
  "September",
  "October",
  "November",
  "December",
];

const getDateByMonth = (date) => {
  const d = date.split("/")[0];
  const month = allMonth[date.split("/")[1] - 1];
  const yr = date.split("/")[2];
  return `${d} ${month} ${yr}`;
};
